
.wrap {
    width: 100%;
    overflow: visible;
}

.container {
    width: $content_width;
    margin: 0 auto;
    position: relative;
    @media(max-width: $content_width) {
        width: 90%;
    }
}

.row {
    display: flex;
    justify-content: space-between;
    flex-flow: row wrap;
}

.align-items-center {
    align-items: center;
}

.col-2 {
    width: 50%;
    position: relative;
    box-sizing: border-box;
}

.col-3 {
    width: 33.3%;
    position: relative;
    box-sizing: border-box;
}

.col-4 {
    width: 25%;
    position: relative;
    box-sizing: border-box;
}

.mobile_hide {
    @media(max-width: $content_width) {
        display: none !important;
    }
}

.mobile_show {
    display: none;
    @media(max-width: $content_width) {
        display: block !important;
    }
}



