.uslugi {
    padding-top: 70px;
    padding-bottom: 70px;
    @media(max-width: $content_width) {
        padding-top: 20px;
        padding-bottom: 20px;
        .col-3 {
            width: 100%;
        }
        img {
            display: block;
            max-width: 100%;
            height: auto;
        }
    }
}

.feedback {
    padding-top: 70px;
    padding-bottom: 70px;
    @media(max-width: $content_width) {
        padding-top: 20px;
        padding-bottom: 20px;
        .col-2 {
            width: 100%;
        }
    }
    &__form {
        border: 3px solid $color_blue;
        padding: 20px 40px;
        @media(max-width: $content_width) {
            padding: 10px 20px;
        }
        input[type="text"], input[type="email"], input[type="tel"], select {
            border: 1px solid #e0e0e0;
            padding: 10px 20px;
            font-size: 18px;
            border-radius: 3px;
            width: 100%;
            box-sizing: border-box;
            background: #fff;
            height: 50px;
        }
        input[type="submit"] {
            border: 2px solid $color_blue;
            font-size: 22px;
            font-weight: bold;
            padding: 10px 20px;
            background: #ffffff;
            cursor: pointer;
            color: $color_blue;
        }
    }
}

.uslugi + .feedback {
    padding-top: 0;
    padding-bottom: 0;
    @media(max-width: $content_width) {
        padding-bottom: 20px;
    }
}



