.avto-trade {
    padding-top: 10px;
    padding-bottom: 70px;
    @media(max-width: $content_width) {
        .col-4 {
            width: 100%;
        }
    }
    &__item {
        margin: 15px auto;
        max-width: 260px;
        position: relative;
        border: 1px solid $color_blue;
        box-sizing: border-box;
        @media(max-width: $content_width) {
            max-width: 100%;
        }
        &-image {
            width: 100%;
            box-sizing: border-box;
            padding: 0;
            margin: 0;
            position: relative;
            display: block;
            height: 180px;
            @media(max-width: $content_width) {
                height: auto;
            }
            img {
                display: block;
                width: 100%;
                height: auto;
            }
        }
        .title {
            text-decoration: none;
            font-size: 16px;
            color: $color_black;
            font-weight: bold;
            padding: 10px;
            height: 42px;
            box-sizing: border-box;
            overflow: hidden;
            display: block;
            white-space: nowrap;
        }
        .price {
            color: $color_blue;
            font-size: 18px;
            padding: 0 10px;
            box-sizing: border-box;
        }
        .year-way {
            color: $color_gary;
            font-size: 16px;
            padding: 10px;
            box-sizing: border-box;
        }
        .button {
            width: 100%;
        }
    }
    &-slider {
        padding-top: 40px;
        padding-bottom: 40px;
        background: #eeeeee;
        .title-h2 {
            font-size: 2em;
            margin-bottom: 0.67em;
            @media(max-width: $content_width) {
                margin-bottom: 85px;
                text-align: center;
            }
        }
    }
}

.avto-trade__single {
    &-image {
        width: 90%;
        height: 300px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        margin-bottom: 40px;
    }
    &-param {
        font-size: 20px;
        padding: 8px 0;
    }
}



