/* Slider */
.slick-loading .slick-list {
    background: #fff;
}

/* Arrows */
.slick-prev,
.slick-next {
    font-size: 0;
    line-height: 0;

    position: absolute;
    top: 50%;

    display: block;

    width: 20px;
    height: 20px;
    padding: 0;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);

    cursor: pointer;

    color: transparent;
    border: none;
    outline: none;
    background: transparent;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
    opacity: 0.8;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
    opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
    opacity: .25;
}

.slick-prev:before,
.slick-next:before {
    font-size: 60px;
    line-height: 1;

    opacity: .75;
    color: white;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
    left: 100px;
    z-index: 9999;
    width: 80px;
    height: 80px;
    background: rgba(255,255,255,0.4);
    border-radius: 50%;
    @media(max-width: $content_width) {
        left: 10px;
        background: transparent;
    }
}

[dir='rtl'] .slick-prev {
    right: 100px;
    left: auto;
    @media(max-width: $content_width) {
        right: 10px;
    }
}

.slick-prev:before {
    content: '\276C';
}

[dir='rtl'] .slick-prev:before {
    content: '\276D';
}

.slick-next {
    right: 100px;
    z-index: 9999;
    width: 80px;
    height: 80px;
    background: rgba(255,255,255,0.4);
    border-radius: 50%;
    @media(max-width: $content_width) {
        right: 10px;
        background: transparent;
    }
}

[dir='rtl'] .slick-next {
    right: auto;
    left: 100px;
    @media(max-width: $content_width) {
        left: 10px;
    }
}

.slick-next:before {
    content: '\276D';
}

[dir='rtl'] .slick-next:before {
    content: '\276C';
}

/* Dots */
.slick-dotted.slick-slider {
    margin-bottom: 30px;
}

.slick-dots {
    position: absolute;
    bottom: -25px;

    display: block;

    width: 100%;
    padding: 0;
    margin: 0;

    list-style: none;

    text-align: center;
}

.slick-dots li {
    position: relative;

    display: inline-block;

    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;

    cursor: pointer;
}

.slick-dots li button {
    font-size: 0;
    line-height: 0;

    display: block;

    width: 20px;
    height: 20px;
    padding: 5px;

    cursor: pointer;

    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
    outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
    opacity: 1;
}

.slick-dots li button:before {
    font-size: 6px;
    line-height: 20px;

    position: absolute;
    top: 0;
    left: 0;

    width: 20px;
    height: 20px;

    content: '•';
    text-align: center;

    opacity: .25;
    color: black;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
    opacity: .75;
    color: black;
}

.slider {
    width: 100%;
    &__image {
        height: 600px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        @media(max-width: $content_width) {
            height: 200px;
        }
    }
}

.js-avto-trade-slider {
    .slick-prev, .slick-next {
        position: absolute;
        top: -40px;
        float: right;
        width: 45px;
        height: 45px;
        border-radius: 3px;
        background: #dddddd;
        &:before {
            color: #999999;
            font-size: 35px;
        }
    }
    .slick-prev {
        left: auto;
        right: 110px;
        @media(max-width: $content_width) {
            right: calc(50% + 40px - 22px);
        }
    }
    .slick-next {
        right: 40px;
        @media(max-width: $content_width) {
            right: calc(50% - 40px - 22px);
        }
    }
}



