.footer {
    background: #1c1c1c;
    color: $color_white;
    padding: 20px 0;
    &__logo {
        max-width: 300px;
        img {
            display: block;
            width: 100%;
            height: auto;
        }
    }
    .inf-item {
        width: 210px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: nowrap;
        @media(max-width: $content_width) {
            margin-left: auto;
            margin-right: auto;
        }
        &__icon {
            font-size: 35px;
            color: $color_blue;
            width: 45px;
        }
        &__content {
            flex: 1;
            font-size: 14px;
            color: $color_secodary;
            height: 60px;
            padding-top: 4px;
            box-sizing: border-box;
        }
        strong {
            color: $color_white;
            font-size: 16px;
            display: block;
            font-weight: bold;
        }
    }
}



