.header {
    padding: 10px 0;
    @media(max-width: $content_width) {
        .row {
            display: block;
            text-align: center;
        }
    }
    &__logo {
        max-width: 300px;
        @media(max-width: $content_width) {
            max-width: 100%;
            width: 100%;
        }
        img {
            display: block;
            width: 100%;
            height: auto;
        }
    }
    .inf-item {
        width: 210px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: nowrap;
        padding-left: 30px;
        @media(max-width: $content_width) {
            width: 210px;
            padding-left: 0;
            margin-left: auto;
            margin-right: auto;
        }
        &:first-child {
            flex: 1;
            padding: 0;
        }
        &__icon {
            font-size: 35px;
            color: $color_blue;
            width: 45px;
        }
        &__content {
            flex: 1;
            font-size: 14px;
            color: $color_secodary;
            height: 60px;
            padding-top: 4px;
            box-sizing: border-box;
        }
        strong {
            color: $color_primary;
            font-size: 16px;
            display: block;
            font-weight: bold;
        }
    }
}

.nav {
    background: $color_blue;
    color: $color_white;
    @media(max-width: $content_width) {
        position: absolute;
        z-index: 9999999;
        left: 0;
        top: 0;
        width: 100%;
        box-sizing: border-box;
    }
    .menu {
        list-style: none;
        margin: 0;
        padding: 0;
        &-item {
            display: inline-block;
            @media(max-width: $content_width) {
                width: 100%;
                box-sizing: border-box;
            }
            a {
                display: block;
                box-sizing: border-box;
                padding: 15px 20px;
                color: $color_white;
                text-decoration: none;
                transition: .2s;
                @media(max-width: $content_width) {
                    width: 100%;
                    text-align: center;
                }
                &:hover {
                    background: rgba(255,255,255, 0.2);
                }
            }
        }
    }
    .mobile_show {
        border-bottom: 1px solid $color_white;
        .title {
            font-size: 20px;
            padding: 10px 0;
        }
    }
    &-mob {
        display: none;
        background: $color_blue;
        color: $color_white;
        @media(max-width: $content_width) {
            display: block;
        }
        .button {
            display: block;
            margin-left: auto;
            margin-right: auto;
            font-size: 18px;
            padding: 15px 20px;
        }
    }
}



