.services {
    padding-top: 10px;
    padding-bottom: 10px;
    &-image {
        width: 25%;
        height: 200px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        margin-bottom: 45px;
        box-sizing: border-box;
        @media(max-width: $content_width) {
            width: 100%;
        }
    }
    &-content {
        width: 75%;
        height: 200px;
        padding-left: 45px;
        margin-bottom: 45px;
        box-sizing: border-box;
        @media(max-width: $content_width) {
            width: 100%;
            height: auto;
            padding-left: 0;
        }
        .title {
            color: $color_black;
            font-size: 20px;
            text-decoration: none;
            font-weight: bold;
        }
        .content-text {
            height: 100px;
            position: relative;
            @media(max-width: $content_width) {
                height: auto;
            }
        }
    }
}

.services-front {
    padding-top: 40px;
    padding-bottom: 40px;
    @media(max-width: $content_width) {
        .col-3 {
            width: 100%;
        }
    }
    &-image {
        display: block;
        width: 90%;
        height: 200px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        margin-bottom: 45px;
        box-sizing: border-box;
        @media(max-width: $content_width) {
            width: 100%;
            margin-bottom: 30px;
        }
    }
    &-content {
        width: 90%;
        margin-bottom: 45px;
        box-sizing: border-box;
        .title {
            color: $color_black;
            font-size: 20px;
            text-decoration: none;
            font-weight: bold;
        }
        .content-text {
            position: relative;
        }
    }
    .title-h2 {
        font-size: 2em;
        margin-bottom: 0.67em;
    }
}



